import React, { useState, useEffect, useContext } from "react";
import ICONS from "../../../../../img/icons.svg";
import ActionButton from "../../../common/ActionButton";
import Input from "../../../common/Input";
import Notification from "../../../common/Notification";
import { toMaxDecimalsRound } from "../../../utils";
import Web3Context from "../../../../../Web3Context";
import { getOwnedCrucibles } from "../../../../../contracts/getOwnedCrucibles";
import { unstakeAndClaim } from "../../../../../contracts/unstakeAndClaim";
import { sendNFT } from "../../../../../contracts/sendNFT";
import { withdraw } from "../../../../../contracts/withdraw";
import Modal from "../../../../Modal";

interface OperatePaneProps {
  handleInputChange?: (form: { [key: string]: string | number }) => void;
  isConnected: boolean;
}

const OperatePane: React.FC<OperatePaneProps> = (props) => {
  const { handleInputChange = () => null, isConnected } = props;

  const { onboard, signer, provider, monitorTx } = useContext(Web3Context);

  const [amount2Withdraw, setAmount2Withdraw] = useState("");
  const [sendAddress, setSendAddress] = useState("");
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [modalOperation, setModalOperation] = useState<"withdraw" | "unstake" | 'send'>(
    "unstake"
  );
  const [selectedCrucible, setSelectedCrucible] = useState("");

  const [formValues, setFormValues] = useState({
    lnBalance: "",
    tbtcBalance: "",
    linearFee: "",
    constantFee: "",
    nodeAddress: "",
  });

  const [crucibles, setCrucibles] = useState(
    [] as {
      id: string;
      balance: string;
      lockedBalance: string;
    }[]
  );
  useEffect(() => {
    getOwnedCrucibles(signer, provider).then(setCrucibles);
  }, [isConnected, provider, signer]);

  const onChange = (ev: React.ChangeEvent<HTMLInputElement>) => {
    //setXAmount is the amount displayed in the input, should be string
    const name = ev.target.name;
    let value = ev.target.value;
    if (ev.target.type === "number")
      value =
        ev.target.value === ""
          ? ev.target.value
          : toMaxDecimalsRound(ev.target.value, +ev.target.step).toString();

    setFormValues((old) => {
      return {
        ...old,
        [name]: value,
      };
    });
  };

  useEffect(() => {
    handleInputChange(formValues);
  }, [formValues, handleInputChange]);

  //todo
  const formatAmount2Withdraw = (ev: React.ChangeEvent<HTMLInputElement>) => {
    let amount = ev.target.value;

    setAmount2Withdraw(amount);
  };

  //todo
  const unstake = async () => {
    await unstakeAndClaim(signer, monitorTx, selectedCrucible, amount2Withdraw);
    setModalIsOpen(false);
  };
  const withdrawTokens = async () => {
    await withdraw(selectedCrucible, amount2Withdraw);
    setModalIsOpen(false);
  };

  const sendModal = <Modal
  title="Send"
  isOpen={modalIsOpen}
  buttonText="Send"
  onButtonClick={async ()=>{
    await sendNFT(signer, selectedCrucible, sendAddress);
    setModalIsOpen(false);
  }}
  onCloseClick={() => setModalIsOpen(false)}
>
  <div style={{ marginBottom: "2rem" }}>Address to send to</div>
  <div className="form-group">
    <Input
      value={sendAddress}
      name="to"
      type="string"
      label="Address "
      onChange={(ev)=>setSendAddress(ev.target.value)}
    />
  </div>
</Modal>

  return (
    <div className="tab-pane is_active">
      <div className="tab-pane__content">
        <div className="box-operation__content">
          <div className="box-operation__operate operate">
            <div className="operate__form">
            {modalOperation === 'send'?sendModal:
              <Modal
                title={modalOperation === "withdraw" ? "Withdraw" : "Unstake"}
                isOpen={modalIsOpen}
                buttonText={
                  modalOperation === "withdraw" ? "Withdraw" : "Unstake"
                }
                onButtonClick={
                  modalOperation === "withdraw" ? withdrawTokens : unstake
                }
                onCloseClick={() => setModalIsOpen(false)}
              >
                <div style={{ marginBottom: "2rem" }}>Input the amount</div>
                <div className="form-group">
                  <Input
                    value={amount2Withdraw}
                    name="balance"
                    type="number"
                    label="Amount "
                    placeholder="0.0"
                    onChange={formatAmount2Withdraw}
                  />
                </div>
              </Modal>
              }
              {crucibles.map((crucible) => {
                return (
                  <div className="crucible-item">
                    <span className="crucible-attribute">
                      <span className="crucible-label">Balance:</span>{" "}
                      {`${crucible["balance"]} (${crucible["lockedBalance"]} locked)`}
                    </span>
                    <span
                      className="crucible-attribute"
                      style={{ flexGrow: 1 }}
                    >
                      <span className="crucible-label">ID:</span>{" "}
                      {crucible["id"]}
                    </span>
                    <span
                      className=""
                      style={{
                        marginRight: "1em",
                      }}
                    >
                      <ActionButton
                        text="Unstake"
                        type="primary"
                        className="crucible-withdraw"
                        buttonStyle={{
                          fontSize: "1rem",
                          padding: "0rem 1rem",
                          minWidth: "0rem",
                        }}
                        onClick={() => {
                          setModalOperation("unstake");
                          setSelectedCrucible(crucible["id"]);
                          setModalIsOpen(true);
                        }}
                      />
                    </span>
                    <span className=""
                    style={{
                      marginRight: "1em",
                    }}>
                      <ActionButton
                        text="Withdraw"
                        type="primary"
                        className="crucible-withdraw"
                        buttonStyle={{
                          fontSize: "1rem",
                          padding: "0rem 1rem",
                          minWidth: "0rem",
                        }}
                        onClick={() => {
                          setModalOperation("withdraw");
                          setSelectedCrucible(crucible["id"]);
                          setModalIsOpen(true);
                        }}
                      />
                    </span>
                    <span className="">
                      <ActionButton
                        text="Send"
                        type="primary"
                        className="crucible-withdraw"
                        buttonStyle={{
                          fontSize: "1rem",
                          padding: "0rem 1rem",
                          minWidth: "0rem",
                        }}
                        onClick={() => {
                          setModalOperation("send");
                          setSelectedCrucible(crucible["id"]);
                          setModalIsOpen(true);
                        }}
                      />
                    </span>
                  </div>
                );
              })}
              {isConnected ? (
                <></>
              ) : (
                <ActionButton
                  text="Connect wallet"
                  className="operate__button"
                  type="secondary"
                  onClick={() => onboard.walletSelect()}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OperatePane;
